}
.whatsapp-icon {
  color:#0c460e; /* Blue for email */
  animation: oscillateRotate 2s ease-in-out infinite; /* Adjust the duration and easing as needed */

}
.facebook-icon {
  color: #16578d; /* Blue for email */
  animation: oscillateRotate 2s ease-in-out infinite; /* Adjust the duration and easing as needed */

}
/* Additional styling as needed */

