}

/* Responsive Design */
@media (max-width: 768px) {
  body {
    padding-top: 60px; /* Adjust this for mobile view if needed */
  }
  .nav-links {
    display: none; /* Hide the links by default on mobile */
    flex-direction: column; /* Stack the links vertically */
    width: 100%; /* Full width */
    position: absolute;
    top: 60px; /* Height of the nav bar */
    left: 0;
    background-color: #b4c2d2; /* Match your navbar color */
    color: #ccc;
  }

  .nav-links.mobile-menu {
    display: flex; /* This will show the menu */
  }

  .mobile-menu-icon {
    display: block; /* Show the menu icon on mobile */
  }
  .hero {
    flex-direction: column;
    text-align: center;
  }

  .hero-image {
    margin-bottom: 30px;
  }

  .hero-content {
    padding-left: 0;
  }
  .about, .services {
    flex-direction: column;
  }

  .card, .about .card {
    width: 80%; /* Adjust based on your preference */
    margin: 10px auto;
  }

  header nav ul {
    flex-direction: column;
    width: 100%;
  }

  header nav ul li {
    text-align: center;
    width: 100%;
  }
  .divider {
    width: 80%; /* Width when in a horizontal layout */
    height: 2px; /* Thickness in horizontal layout */
    margin: 20px auto; /* Centering the divider horizontally */
  }
  .service-card {
    flex-basis: 45%; /* Adjust for smaller screens */
  }
}
@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}
@keyframes oscillateRotate {
  0%, 100% {
    transform: rotate(-10deg); /* Rotates 10 degrees to the left */
  }
  50% {
    transform: rotate(10deg); /* Rotates 10 degrees to the right */
  }
}
@keyframes verticalFlip {
  0%, 100% {
    transform: rotateX(0deg);
  }
  50% {
    transform: rotateX(90deg);
  }
}
/* Keyframes for fade-in effect */
@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}


